<template>
  <VueApexCharts
    v-if="!isLoading"
    type="line"
    width="100%"
    :options="chartOptions"
    :series="series"
  />
  <div v-else class="container"> </div>
</template>

<script>
import VueApexCharts from "vue3-apexcharts";

export default {
  components: {
    VueApexCharts,
  },
  data() {
    return {
      api: "https://script.google.com/macros/s/AKfycbwQUMHFzgMkQMOjJHDj9286__c_ViUqQQX3Xg2yctKJHyHC9yrh-TGjujQtOYA5bp5KCA/exec",
      isLoading: true,
      date: [],
      peak_loading: [],
      reserve_capacity: [],
      reserve_rate: [],

      chartOptions: {
        chart: {
          height: 350,
          zoom: { enabled: false },
        },
        title: {
          text: "Taipower-chart",
        },
        tooltip: {
          x: {
            show: false,
            // formatter: (val, opts) =>
            //   opts.w.globals.lastXAxis.categories[opts.dataPointIndex],
          },
        },
        xaxis: {
          type: "category",
          categories: ["2022/01/01", "2022/01/02", "2022/01/03"],
          labels: {
            // formatter: (val) => {
            //   const date = new Date(val);
            //   return date.getDate() === 1
            //     ? `${date.getFullYear()}${date.getMonth() + 1}`
            //     : val;
            // },
            rotate: -10,
          },
          // tooltip: {
          //   formatter: function (val, opts) {
          //     console.log(val);
          //   },
          // },
        },
        yaxis: [
          {
            seriesName: "",
            //seriesName: "peak_loading",
            max: 4800,
            min: 0,
            axisBorder: { show: true },
            title: { text: "" },
            labels: { formatter: (val) => val },
          },
          {
            seriesName: "",
            // seriesName: "peak_loading",
            show: false,
          },
          {
            seriesName: "",
            // seriesName: "reserve_rate",
            opposite: true,
            max: 30,
            min: 0,
            axisBorder: { show: true },
            title: { text: "" },
            labels: { formatter: (val) => `${val}%` },
          },
        ],
      },
      series: [
        {
          name: "",
          // name: "peak_loading",
          type: "line",
          data: [2566.1, 2591.5, 2997.6],
        },
        {
          name: "",
          // name: "reserve_capacity",
          type: "column",
          data: [334.5, 364.1, 318.4],
        },
        {
          name: "",
          // name: "reserve_rate",
          type: "area",
          data: [13.04, 14.05, 10.62],
        },
      ],
    };
  },
  methods: {
    updateChart() {
      //X
      this.chartOptions = {
        ...this.chartOptions,
        xaxis: { ...this.chartOptions.xaxis, categories: this.date },
      };

      // console.log("config", {
      //   ...this.chartOptions,
      //   xaxis: { ...this.chartOptions.xaxis, categories: this.date },
      // });

      //Y
      this.series = this.series.map((item) => ({
        ...item,
        data: this[item.name],
      }));
    },
    getTaipower() {
      this.$http
        .get(this.api)
        .then((resp) => {
          if (resp.data?.status !== "success") throw Error(resp.data?.message);

          const data = resp.data?.data;

          this.date = data.map((item) => item.date);
          this.peak_loading = data.map((item) => item.peak_loading);
          this. = data.map((item) => item.peak_loading);
          this. = data.map((item) => item.reserve_capacity);
          this. = data.map((item) => item.reserve_rate);

          this.updateChart();

          this.isLoading = false;
        })
        .catch((err) => {
          console.log(err?.message);
        });
    },
  },
  mounted() {
    this.getTaipower();
    // this.isLoading = false;
  },
};
</script>
